import * as React from "react";

import { Query } from "react-apollo";
import { CERTIFICATE_PRICE_QUERY } from "../../../shared/Queries";

import Alert from "../../../components/ui/Alert";
import ButtonNext from "../../../components/ui/ButtonNext";
import ButtonPrev from "../../../components/ui/ButtonPrev";
import Col from "../../../components/ui/Col";
import FormCheck from "../../../components/ui/FormCheck";
import FormStep from "../../../components/ui/FormStep";
import FormText from "../../../components/ui/FormText";
import Row from "../../../components/ui/Row";
import Spinner from "../../../components/ui/Spinner";
import { LicenseSteps } from "../../../shared/Model";

class Approval extends FormStep {
  constructor(props: any) {
    super(props);

    this.prevStep = this.prevStep.bind(this);
    this.nextStep = this.nextStep.bind(this);
  }

  public render() {
    return (
      <Query query={CERTIFICATE_PRICE_QUERY}>
        {({ loading, error, data }) => {
          if (loading) {
            return (
              <p>
                <Spinner />
              </p>
            );
          }

          if (error) {
            return <p>Fout...</p>;
          }

          return (
            <div>
              <Row>
                <Col>
                  <Alert type="info">
                    Geef aan of u akkoord gaat met de kosten en voeg optioneel
                    een opmerking toe.
                  </Alert>
                </Col>
              </Row>
              <Row>
                <Col>
                  <FormText
                    id="remarks"
                    label="Opmerkingen"
                    name="FormOptions.Remarks"
                    isTextArea={true}
                    placeholder="Voeg optioneel een opmerking toe"
                    form={this.props}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <FormCheck
                    id="approved"
                    label="U kent de kosten voor aanvraag"
                    name="FormOptions.Approved"
                    form={this.props}
                    help={`De prijs voor de aanvraag is ${data
                      .tariefByCertificaatCode.TotaalExtBtw ||
                      "?"} Euro (excl. btw)`}
                  />
                </Col>
              </Row>
              <ButtonPrev onClick={this.prevStep} />
              <ButtonNext
                label="Naar samenvatting aanvraag"
                disabled={!this.props.values.FormOptions.Approved}
                onClick={this.nextStep}
              />
            </div>
          );
        }}
      </Query>
    );
  }

  private prevStep() {
    this.setStep(
      this.props.values.FormOptions.Vooropleiding &&
      ["30.02", "30.03"].indexOf(
        this.props.values.FormOptions.Vooropleiding.Code
      ) > -1
        ? LicenseSteps.DocumentAG
        : LicenseSteps.Document
    );
  }

  private nextStep() {
    this.setStep(LicenseSteps.Summary);
  }
}

export default Approval;
